#app {
  position: relative;
}

.content {
  position: absolute;
  top: 0;
  right: 0;
  left: 12.5rem;
}

.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  width: 12.5rem;
  padding: 0.625rem;
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, 0.1);
  overflow-y: auto;
}
